.hownd-three-col {
  padding-top: 65px;
  padding-bottom: 65px;
  font-size: 20px;
}
.hownd-three-col > .acf-innerblocks-container {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}
.hownd-three-col__column {
  padding: 15px;
}
.hownd-three-col__column.is-style-align-center {
  align-self: center;
}
.hownd-three-col__column > .acf-innerblocks-container > *:last-child {
  margin-bottom: 0;
}
@media screen and (min-width: 992px) {
  .hownd-three-col__column {
    flex: 0 0 33.33%;
    max-width: 33.33%;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .hownd-three-col__column {
    flex: 0 0 50%;
    max-width: 50%;
  }
  .hownd-three-col__column:last-child {
    flex: 0 0 100%;
    max-width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .hownd-three-col {
    padding-top: 15px;
    padding-bottom: 15px;
    text-align: center;
  }
  .hownd-three-col .wp-block-buttons {
    justify-content: center;
  }
  .hownd-three-col__column {
    flex: 0 0 100%;
    max-width: 100%;
  }
}